import { useState, useEffect } from "react"
import { useNavigate } from "react-router-dom"
import axios from "axios"

const apiUrl = "https://www.pre-onboarding-selection-task.shop/auth/signin"

const httpRequestHeader = {
  headers: {
    "Content-Type": "application/json",
  },
}

const useSignin = () => {
  const navigate = useNavigate()
  const [emailAddress, setEmailAddress] = useState("")
  const [passWord, setPassword] = useState("")
  const emailPasswordData = { email: emailAddress, password: passWord }
  const [disable, setDisable] = useState(true)
  const [submit, setSubmit] = useState(false)

  const handleChangeEmailAddress = (e) => {
    e.preventDefault()
    const { value } = e.target
    setEmailAddress(value)
  }

  const handleChangePassword = (e) => {
    e.preventDefault()
    const { value } = e.target
    setPassword(value)
  }

  const onSubmit = async (e) => {
    e.preventDefault()
    try {
      setSubmit(true)
      const response = await axios.post(
        apiUrl,
        emailPasswordData,
        httpRequestHeader,
      )
      alert("Î°úÍ∑∏Ïù∏ÎêòÏóàÏäµÎãàÎã§. üòé")
      localStorage.setItem("access_token", response.data.access_token)
      navigate("/todo")
      return response.data.access_token
    } catch (error) {
      alert("Ïù¥Î©îÏùº ÌòπÏùÄ ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÌãÄÎ¶∞Í±∞..Í∞ôÏùÄÎç∞Ïöî?üòì")
    } finally {
      setSubmit(false)
    }
  }

  useEffect(() => {
    const accessToken = localStorage.getItem("access_token")
    if (accessToken) {
      alert("Ïù¥ÎØ∏ Î°úÍ∑∏Ïù∏Ïù¥ ÎêòÏûàÏúºÏãúÎÑ§Ïöî?ü§î")
      navigate("/todo")
    }
  }, [navigate])

  useEffect(() => {
    if (emailAddress.includes("@") && passWord.length >= 8) {
      setDisable(false)
    } else {
      setDisable(true)
    }
  }, [emailAddress, passWord])

  return {
    emailAddress,
    passWord,
    submit,
    disable,
    handleChangeEmailAddress,
    handleChangePassword,
    onSubmit,
  }
}

export default useSignin
